<template>
    <div class="economy">
        <h1>
            Economy News
        </h1>
        <economicnews v-for="econonew in econonews" 
                    :key="econonew.id" 
                    :date="econonew.date" 
                    :category="econonew.category" 
                    :title="econonew.title" 
                    :discrption="econonew.discrption"
                    :imgSrc="econonew.imgsrc"
                    :pstNo="econonew.pstno" />
       
        <div  id="view-box-details">
            <div class="details">
        
        <div class="row">

            <div id="details-cat" class="col-sm-6 details-econo-category ">
               {{category}} 
            </div>
            <div id="details-dat" class="col-sm-6 details-econo-date ">
               <p> {{date}}</p>
            </div>
            <div  class="col-sm-3 details-exit-btn">
                <button @click="closeDetailsWindow" class="det-ex-btn">X</button>
            </div>
        </div>

        <div id="details-tit" class=" details-econo-title">
            <h4> {{title}}</h4>
        </div>
        <div  class="details-econo-img img-det">
            <img id="details-img" :src="this.imgSrc" :alt="alte">
            
        </div>
        <div id="details-dis" class="details-econo-discrptions">
            {{discrption}}
        </div>


    
            </div>
        </div>
    </div>

</template>

<script>
import economicnews from '../components/economicnews.vue';
import EconoNewsJson from "../json/econnews.json";
export default {
  name: "EconomyPage",
  components: { 
      economicnews
    },
    data: function() {
        return {
            econonews: EconoNewsJson,
        };
    },
    methods: {
        closeDetailsWindow : function(){
            document.getElementById("view-box-details").style.display = 'none';
            this.style.setProperty('color','red');
            alert('closed button');
        },
    },
    
}
</script>
<style>
@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Source+Sans+Pro&display=swap');
</style> 
<style lang="scss" >
$orangeCol: #FEC194;
//$redCol: #FF0061;
$redCol:rgb(243, 44, 120);
$darkblueCol:#2b3f53;

.economy{
    padding-top: 20px;
    h1{
      font-family: 'Lobster', cursive;
      color:$redCol ;
      padding-bottom: 20px;
     }
    #view-box-details{
        width: 100%;
        height: 650px;
        position: fixed;
        padding-left: 250px ;
        top: 0;
        background-color: rgba(0, 0, 0, 0.829);
        z-index: 2;
        display: none;
        
        .details{
        width: 850px;
        height: 550px;
        top: 50px;
        background-color: $orangeCol;
        border-radius: 5%;
        position: fixed;
        padding: 25px 25px 25px 25px ;
        
        .row{
            margin-bottom: 2px;
            padding: 0px 10px 0px 10px;
        }
        .details-econo-category {
            width: 70px;
            height: 40px;
            text-align: center;
            background-color: $darkblueCol;
            color: $orangeCol;
            padding-top: 6px;
            border-radius: 45%;
        }
        .details-econo-date{
        height: 40px;
        text-align: left;
        padding-top: 6px;
        opacity: 0.6;
        } 
        .details-exit-btn{
            width: 31px;
            height: 31px;
            position: absolute;
            right: 35px;
            button{
            width: 30px;
            height: 30px;
            border: 1px solid $darkblueCol;
            background-color: $darkblueCol;
            color: $orangeCol;
            }
            button:hover{
                background-color: $redCol;
                color: $darkblueCol;
                transition: ease-in-out 0.1s;
            }
        }
        .details-econo-title{
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        font-weight: bolder;
        margin-top: 15px;
        width: 400px;
        height: 100px;
        float: left;
        text-align: left;
        left: 0;
        color: $darkblueCol;
        font-size: 20px;
        }
        .details-econo-img{
            width: 310px;
            height: 150px;
            position: absolute;
            right: 25px;
            
            img{
                width: 310px;
                height: 150px;
                border:2px solid $darkblueCol;
                border-top-left-radius: 20%;
                border-top-right-radius: 20%;
                background-color: $darkblueCol;
                
            }
        }
        .details-econo-discrptions{
            width: 800px;
            height: 310px;
            margin-top: 160px;
            background-color: $darkblueCol;
            color: $orangeCol;
            text-align: left;
            padding: 5px 10px 5px 10px;
            font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
            box-shadow: 2px 2px 18px 0px $darkblueCol;
            overflow-y: scroll;
        }
        
        }
    }
}
@media screen and (max-width: 600px) {
  .economy #view-box-details{
    padding-left: 8px;
    height: 100%;
    z-index: 11;
    .details{
      top: 30px;
      width: 95%;
      padding-left: 10px;
      padding-bottom: 20px;
      overflow: hidden;
      overflow-y: scroll;
      #details-cat{
          font-size: 10px;
      }
      #details-tit{
        float: initial;
        width: 100%;
        font-size: 17px;
      }
      .img-det{
        width:100%;
        right: 0px;
        #details-img{
          width: 90%;
        }
      }
      #details-dis{
        width: 100%;
        padding: 15px 10px 15px 10px;
      }
    }
  }

  
}
</style>